.font-link {
    font-family: 'Gotham';
}

.App-logo {
    max-width: 100%;
    height: auto;
    padding: 0;
    margin: 0;
    align-self: center;
}

.Div-text {
    justify-content: center;
    align-items: center;
    display: flex;
}

.text-30 {
    font-size: 30px;
}

.text-50 {
    font-size: 50px;
}


.Div-middle-title {
    width: 60%;
    height: 100%;
    padding-inline: 5%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

.Div-image {
    height: 70%;
}

.Book-now {
    width: 200px;
}

.S2-title {
    font-size: 30px;
    color: #fff;
    font-weight: 300;
}

.S2-hr {
    margin: 0;
    width: 30px;
    margin-top: 20px;
    margin-bottom: 15px;
    border: none;
    border-bottom: 1px solid #999999;
    will-change: transform;
}

.S2-p1 {
    font-size: 20px;
    color: #fff;
}

.S2-p2 {
    
}

.S2-card {
    padding: 16px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}